.navbar {
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  margin-top: 24px;
  padding: 10px 12vw;

  &__links {
    border-bottom: 2px solid transparent;
    transition: border-bottom 75ms ease;

    &:hover {
      border-color: white;
    }
  }
}
